<style lang="scss">
.sign-up-form {
  input {
    border: 0;
    padding: 0;
    border-bottom: 1px solid $color-gray-normal;
  }

  .submit-btn {
    width: 100%;
  }

  .others {
    margin-bottom: 10px;
    @extend %clearfix;

    a {
      font-size: 12px;
    }

    .fl {
      float: left;
    }

    .fr {
      float: right;
    }
  }

  .tip {
    text-align: left;
    line-height: 25px;
    font-size: 13px;
    color: $color-text-normal;
  }

  .watch-pwd {
    position: absolute;
    right: 36px;
    top: 11px;
    font-size: 18px;
    color: $color-gray-deep;
  }

  .providers {
    float: left;
    margin-top: -5px;

    span {
      font-size: 12px;
      line-height: 23px;
    }

    i {
      font-size: 20px;
      vertical-align: middle;
      margin-right: 10px;
      color: $color-text-normal;
      cursor: pointer;
    }

    .icon-qq:hover {
      color: #3194d0;
    }

    .icon-wechat-copy:hover {
      color: #42c02e;
    }
  }
}
</style>

<template>
  <div class="sign-up-form">
    <el-form
      ref="form"
      :model="form"
      :rules="rule"
      status-icon
    >
      <el-form-item prop="nickname">
        <el-input
          v-model="form.nickname"
          type="text"
          placeholder="2-1412"
          auto-complete="off"
        />
      </el-form-item>
      <el-form-item prop="access">
        <el-input
          v-model="form.access"
          type="text"
          placeholder=""
          auto-complete="off"
        />
      </el-form-item>
      <el-form-item prop="secret">
        <el-input
          v-model="form.secret"
          :type="watchPwd ? 'text' : 'password'"
          placeholder="6-16"
          auto-complete="off"
        />
        <button
          v-if="form.secret.length > 5"
          class="watch-pwd"
          type="button"
          @click="watchPwd = !watchPwd"
        ><i class="iconfont icon-yuedu"/></button>
      </el-form-item>
      <el-form-item v-if="!inviteCode">
        <el-input
          v-model="form.inviteCode"
          type="number"
          placeholder=""
          auto-complete="off"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="submitBtnLoading"
          :disabled="submitBtnDisabled"
          class="submit-btn"
          type="primary"
          @click="submitForm"
        >
          {{ submitBtnText }}
          <template v-if="timeout">
            {{ timeout }}s 
          </template>
        </el-button>
      </el-form-item>
    </el-form>
    <div
      class="others"
    >
      <span class="providers">
        <a href="https://api.calibur.tv/callback/oauth2/qq?from=sign">
          <i class="iconfont icon-qq"/>
        </a>
        <a
          v-if="ua.wechat"
          href="https://api.calibur.tv/callback/oauth2/weixin?from=sign"
        >
          <i class="iconfont icon-wechat"/>
        </a>
      </span>
      <a
        v-if="!inviteCode"
        class="fr"
        @click="showLogin"
      ></a>
    </div>
    <div class="tip">
      
    </div>
  </div>
</template>

<script>
import { sendMessage, register } from '~/api/userApi'

export default {
  name: 'SignUpForm',
  props: {
    inviteCode: {
      type: String,
      default: ''
    }
  },
  data() {
    const validateNickname = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error(''))
      }
      const length = value.replace(/([\u4e00-\u9fa5])/g, 'aa').trim().length
      if (length < 2) {
        return callback(new Error('2'))
      }
      if (length > 14) {
        return callback(new Error('1412'))
      }
      if (!/^([\u4e00-\u9fa5]|[a-z0-9])+$/i.test(value)) {
        return callback(new Error(''))
      }
      callback()
    }
    const validateAccess = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      if (value.length !== 11) {
        return callback(new Error('11'))
      }
      if (!/^(0|86|17951)?(1)[0-9]{10}$/.test(value)) {
        return callback(new Error(''))
      }
      callback()
    }
    const validateSecret = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error(''))
      }
      if (value.length < 6) {
        return callback(new Error('6'))
      }
      if (value.length > 16) {
        return callback(new Error('16'))
      }
      callback()
    }
    return {
      watchPwd: false,
      form: {
        access: '',
        secret: '',
        nickname: '',
        authCode: '',
        inviteCode: this.inviteCode
      },
      rule: {
        nickname: [{ validator: validateNickname, trigger: 'blur' }],
        access: [{ validator: validateAccess, trigger: 'blur' }],
        secret: [{ validator: validateSecret, trigger: 'blur' }]
      },
      /**
       * 0
       * ----  0
       * 1...
       * ----  loading
       * ----  geetest 
       * ----  geetest 
       * ----  geetest  step 0
       * ----  step 0
       * ----  step 2
       * ---- 30s  step 0
       * 2
       * ---- 
       * ----  step 2
       * ---- 30s  step 0
       * 3...
       * ----  loading  disabled
       * ---- 
       * ----  step 2 
       * ---- 
       * ----  unique 
       * ----  step 0
       */
      step: 0,
      timeout: 0
    }
  },
  computed: {
    ua() {
      return this.$store.state.ua
    },
    submitBtnText() {
      if (this.step === 0) {
        return ''
      } else if (this.step === 1) {
        return '...'
      } else if (this.step === 2) {
        return ''
      } else if (this.step === 3) {
        return '...'
      }
    },
    submitBtnLoading() {
      return this.step === 1 || this.step === 3
    },
    submitBtnDisabled() {
      return (this.timeout !== 0 && this.step === 0) || this.step === 3
    }
  },
  methods: {
    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.step === 0) {
            this.getRegisterAuthCode()
          }
          if (this.step === 2) {
            this.openConfirmModal()
          }
        } else {
          return false
        }
      })
    },
    getRegisterAuthCode() {
      this.step = 1
      this.$captcha({
        ctx: this,
        success: async ({ data }) => {
          try {
            await sendMessage(this, {
              type: 'sign_up',
              phone_number: this.form.access,
              geetest: data
            })
            this.step = 2
            this.openConfirmModal()
          } catch (e) {
            this.step = 0
          } finally {
            this.timeout = 60
            const timer = setInterval(() => {
              if (!--this.timeout) {
                this.step = 0
                clearInterval(timer)
              }
            }, 1000)
          }
        },
        close: () => {
          this.step = 0
        }
      })
    },
    openConfirmModal() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^\d{6}$/,
        inputErrorMessage: ''
      })
        .then(({ value }) => {
          this.form.authCode = value
          this.step = 3
          this.signUp()
        })
        .catch(() => {})
    },
    signUp() {
      register(this, {
        access: this.form.access,
        secret: this.form.secret,
        nickname: this.form.nickname,
        authCode: this.form.authCode,
        inviteCode: this.form.inviteCode
      })
        .then(res => {
          this.$toast.success('')
          this.$cookie.set('JWT-TOKEN', res)
          window.location.reload()
        })
        .catch(() => {
          this.step = 0
        })
    },
    showOAuth() {
      this.$toast.info('')
    },
    showLogin() {
      this.$emit('to-login')
      this.$refs.form.resetFields()
    }
  }
}
</script>
