<style lang="scss">
#user-report-form {
  margin-top: 10px;

  p {
    font-size: 12px;
    padding: 8px;
    color: #888;
  }

  textarea {
    min-height: 120px;
    background-color: #fafafa;
    padding: 10px;
  }

  .submit-btn {
    margin-top: 15px;
    margin-bottom: 30px;
    width: 100%;
  }
}
</style>

<template>
  <div
    id="user-report-form"
    class="container"
  >
    <v-radio
      v-model="selectedType"
      :options="options1"
      title=""/>
    <v-radio
      v-model="selectedType"
      :options="options2"
      title=""/>
    <v-radio
      v-model="selectedType"
      :options="options3"
      title=""/>
    <div v-if="needContent">
      <p></p>
      <textarea
        v-model.trim="content"
        placeholder=""/>
    </div>
    <el-button
      :loading="loading"
      type="primary"
      class="submit-btn"
      @click="submit"
    ></el-button>
  </div>
</template>

<script>
import { report } from '~/api/userApi'
import VRadio from '~/components/common/Radio'

export default {
  name: 'UserReportForm',
  components: {
    VRadio
  },
  props: {
    id: {
      type: [Number, String],
      default: 0
    },
    model: {
      type: [Number, String],
      default: 0
    }
  },
  data() {
    return {
      selectedType: -1,
      loading: false,
      content: ''
    }
  },
  computed: {
    options1() {
      return [
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        }
      ]
    },
    options2() {
      return [
        {
          label: '',
          value: 4
        },
        {
          label: '',
          value: 5
        },
        {
          label: '',
          value: 6
        }
      ]
    },
    options3() {
      return [
        {
          label: '',
          value: 7
        },
        {
          label: '',
          value: 8
        },
        {
          label: '/',
          value: 9
        },
        {
          label: '',
          value: 10
        },
        {
          label: '',
          value: 11
        },
        {
          label: '',
          value: 99
        }
      ]
    },
    needContent() {
      return this.selectedType === 99 || this.selectedType === 6
    }
  },
  methods: {
    async submit() {
      if (!this.id || !this.model) {
        this.$toast.error('')
        return
      }
      if (this.selectedType < 0) {
        this.$toast.error('')
        return
      }
      if (this.needContent && !this.content) {
        this.$toast.error('')
        return
      }
      if (this.loading) {
        return
      }
      this.loading = true
      try {
        report(this, {
          id: this.id,
          message: this.content,
          type: this.selectedType === 99 ? 0 : this.selectedType,
          model: this.model
        })
      } finally {
        this.loading = false
        this.$toast.success('')
        this.content = ''
        this.selectedType = -1
        this.$emit('success')
      }
    }
  }
}
</script>
