<style lang="scss">
.share-tips-drawer {
  border-radius: 10px 10px 0 0;

  .container {
    text-align: left;

    p {
      margin-top: 20px;
      margin-bottom: 20px;
      line-height: 20px;
      color: #333;
    }

    .btn-submit {
      margin-top: 10px;
    }
  }
}
</style>

<template>
  <v-drawer
    v-model="show"
    class="share-tips-drawer"
    from="bottom"
    size="80%"
    header-text=""
  >
    <div class="container">
      <p>
        calibur.tv""
      </p>
      <p>
        cosplay1 = 1
      </p>
      <p>
        ~\(^o^)/~ http://calibur.tv
      </p>
      <button
        ref="shareBtn"
        class="btn-submit"
        data-clipboard-text="calibur.tv''cosplay1 = 1~\(^o^)/~ http://calibur.tv"
      ></button>
    </div>
  </v-drawer>
</template>

<script>
import Clipboard from 'clipboard'

export default {
  name: 'ShareDrawer',
  data() {
    return {
      show: false
    }
  },
  mounted() {
    this.$channel.$on('open-share', () => {
      this.show = true
      this.bindShareEvent()
    })
  },
  methods: {
    bindShareEvent() {
      this.$nextTick(() => {
        const clipboard = new Clipboard(this.$refs.shareBtn)

        clipboard.on('success', e => {
          this.$toast.success('')
          this.show = false
          clipboard.destroy()
          e.clearSelection()
        })
      })
    }
  }
}
</script>
