export default err => {
  if (/timeout of/.test(err.message)) {
    err.statusCode = 504
    err.message = ''
    return err
  }
  if (!err.response) {
    err.statusCode = 502
    err.message = ''
    return err
  }
  err.statusCode = err.response.status
  err.message = err.response.data.message
  return err
}
