<style lang="scss">
.feedback-drawer {
  text-align: left;

  .desc {
    p {
      margin: 8px 0;
    }

    textarea {
      min-height: 120px;
      background-color: #fafafa;
      padding: 10px;
    }
  }

  .btn-submit {
    margin-top: 15px;
    margin-bottom: 30px;
  }
}
</style>

<template>
  <v-drawer
    v-model="show"
    :backdrop="false"
    class="feedback-drawer"
    from="bottom"
    size="100%"
    header-text=""
  >
    <div class="container">
      <div class="desc">
        <p></p>
        <textarea 
          v-model.trim="content" 
          :placeholder="placeholder"/>
      </div>
      <v-radio
        v-model="selectedType"
        :options="options"
        title=""/>
      <button 
        class="btn-submit" 
        @click="submitFeedback"></button>
    </div>
  </v-drawer>
</template>

<script>
import { feedback } from '~/api/userApi'
import VRadio from '~/components/common/Radio'

export default {
  name: 'FeedbackDrawer',
  components: {
    VRadio
  },
  data() {
    return {
      show: false,
      options: [
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 4
        },
        {
          label: '',
          value: 5
        },
        {
          label: '',
          value: 6
        },
        {
          label: '',
          value: 3
        }
      ],
      selectedType: 1,
      placeholder: '',
      content: '',
      preContent: ''
    }
  },
  mounted() {
    this.$channel.$on('open-feedback', ({ type, desc, placeholder } = {}) => {
      this.selectedType = type || 1
      this.preContent = desc || '{?}'
      this.placeholder =
        placeholder || ''
      this.show = true
    })
  },
  methods: {
    async submitFeedback() {
      if (!this.content.length) {
        this.$toast.error('')
        return
      }
      await feedback(this, {
        type: this.selectedType,
        desc: this.preContent.replace('{?}', this.content),
        ua: navigator.userAgent
      })
      this.$toast.success('')
      this.content = ''
      this.show = false
    }
  }
}
</script>
